  name: "DescriptionForm",
  components: {
    TextInput,
    FilePicker,
    RadioGroup,
    VTypography,
    PopoverContent,
    FormsContainer,
    TechnologyFileItem,
    TypographyWithInfo,
  },
  computed: {
    ...mapGetters("description", [
      "getFiles",
      "getRussianDescriptionProgram",
      "getEnglishDescriptionProgram",
      "getRussianProgramLanguage",
      "getEnglishProgramLanguage",
      "getProgramSize",
      "getProgramSizeUnit",
      "getModifiedStatus",
      "getRussianRegisterNumber",
      "getEnglishRegisterNumber",
      "getUsesPersonalData",
      "getRussianPersonalData",
      "getEnglishPersonalData",
      "getRussianPersonalDataTarget",
      "getEnglishPersonalDataTarget",
    ]),
    russianDescriptionProgram: {
      get() {
        return this.getRussianDescriptionProgram;
      },
      set(value) {
        this.changeRussianDescriptionProgram(value);
      },
    },
    englishDescriptionProgram: {
      get() {
        return this.getEnglishDescriptionProgram;
      },
      set(value) {
        this.changeEnglishDescriptionProgram(value);
      },
    },
    russianProgramLanguage: {
      get() {
        return this.getRussianProgramLanguage;
      },
      set(value) {
        this.changeRussianProgramLanguage(value);
      },
    },
    englishProgramLanguage: {
      get() {
        return this.getEnglishProgramLanguage;
      },
      set(value) {
        this.changeEnglishProgramLanguage(value);
      },
    },
    russianRegisterNumber: {
      get() {
        return this.getRussianRegisterNumber;
      },
      set(value) {
        this.changeRussianRegisterNumber(value);
      },
    },
    englishRegisterNumber: {
      get() {
        return this.getEnglishRegisterNumber;
      },
      set(value) {
        this.changeEnglishRegisterNumber(value);
      },
    },
    russianPersonalData: {
      get() {
        return this.getRussianPersonalData;
      },
      set(value) {
        this.changeRussianPersonalData(value);
      },
    },
    englishPersonalData: {
      get() {
        return this.getEnglishPersonalData;
      },
      set(value) {
        this.changeEnglishPersonalData(value);
      },
    },
    russianPersonalDataTarget: {
      get() {
        return this.getRussianPersonalDataTarget;
      },
      set(value) {
        this.changeRussianPersonalDataTarget(value);
      },
    },
    englishPersonalDataTarget: {
      get() {
        return this.getEnglishPersonalDataTarget;
      },
      set(value) {
        this.changeEnglishPersonalDataTarget(value);
      },
    },
    programSize: {
      get() {
        return this.getProgramSize;
      },
      set(value) {
        this.changeProgramSize(value);
      },
    },
    programSizeUnit: {
      get() {
        return this.getProgramSizeUnit;
      },
      set(value) {
        this.changeProgramSizeUnit(value);
      },
    },
    modifiedStatus: {
      get() {
        return this.getModifiedStatus;
      },
      set(value) {
        this.changeModifiedStatus(value);
      },
    },
    usesPersonalData: {
      get() {
        return this.getUsesPersonalData;
      },
      set(value) {
        this.changeUsesPersonalData(value);
      },
    },
  },
  data() {
    return {
      loading: false,
    };
  },
  methods: {
    ...mapMutations("description", [
      "removeFile",
      "changeRussianDescriptionProgram",
      "changeEnglishDescriptionProgram",
      "changeRussianProgramLanguage",
      "changeEnglishProgramLanguage",
      "changeProgramSize",
      "changeProgramSizeUnit",
      "changeModifiedStatus",
      "changeRussianRegisterNumber",
      "changeEnglishRegisterNumber",
      "changeUsesPersonalData",
      "changeRussianPersonalData",
      "changeEnglishPersonalData",
      "changeRussianPersonalDataTarget",
      "changeEnglishPersonalDataTarget",
    ]),
    ...mapActions("description", ["uploadFiles"]),
    onDeleteClick(index) {
      this.removeFile(index);
    },
    onFilesSelected(files) {
      this.loading = true;

      this.uploadFiles(files).finally(() => {
        this.loading = false;
      });
    },
  },
};
