<template>
  <div class="d-flex align-items-center technology-file-item">
    <FileIcon />
    <VTypography
      font-weight="medium"
      :class="titleClasses"
      class="flex-fill technology-file-item__title"
      >{{ fileName }}</VTypography
    >

    <DeleteButton
      @mouseenter="focus = true"
      @mouseleave="focus = false"
      @click="$emit('delete')"
    />
  </div>
</template>

<script>
import FileIcon from "@/components/icons/FileIcon";
import VTypography from "@/components/ui/Typography";
import DeleteButton from "@/components/buttons/DeleteButton";

export default {
  name: "TechnologyFileItem",
  components: { DeleteButton, FileIcon, VTypography },
  emits: {
    delete: Function,
  },
  props: {
    file: {
      type: Object,
      require: true,
    },
  },
  data() {
    return {
      focus: false,
    };
  },
  computed: {
    titleClasses() {
      return {
        "technology-file-item__title--focus": this.focus,
      };
    },
    fileName() {
      if (this.file?.name.length > 24) {
        const first = this.file?.name.slice(0, 24);
        const last = this.file?.name.slice(-24);

        return `${first} ... ${last}`;
      }

      return this.file?.name;
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.technology-file-item {
  max-width: 556px;
}

.technology-file-item__title {
  margin: 0 5px;
}

.technology-file-item__title--focus {
  color: $red;
}
</style>
